export class Signal<Item = unknown> {
  slots: Slot<Item>[]

  constructor() {
    this.slots = []
  }

  connect(slot: Slot<Item>) {
    this.slots.push(slot)
  }

  disconnect(slot?: Slot<Item>) {
    if (slot) {
      this.slots = this.slots.filter(s => s === slot)
    } else {
      this.slots = []
    }
  }

  async emit(item: Item, parallel = false) {
    if (parallel) {
      await Promise.all(this.slots.map(s => s.exec(item)))
    } else {
      for (const s of this.slots) {
        await s.exec(item)
      }
    }
  }
}

export class Slot<Item = unknown> {
  action: ((item: Item) => Promise<void>)[]

  constructor() {
    this.action = []
  }

  async exec(item: Item) {
    for (const a of this.action) {
      await a(item)
    }
  }

  bind(act: (item: Item) => Promise<void>) {
    this.action.push(act)
  }
}

export class Broadcast<Item = unknown> {
  signal: Signal<Item>
  slot: Slot<Item>

  constructor(parallel = false) {
    this.signal = new Signal()
    this.slot = new Slot()

    this.slot.bind(async item => {
      await this.signal.emit(item, parallel)
    })
  }
}
