import { AsyncQueue } from './queue'
import { Signal, Slot, Broadcast } from './signal'
import { Client, Game } from './types'

class SlaveEventLoop<
  InputMessage,
  OutputMessage,
  G extends Game<InputMessage, OutputMessage>
> {
  game: SlaveGame<InputMessage, OutputMessage, G>

  constructor(g: SlaveGame<InputMessage, OutputMessage, G>) {
    this.game = g
  }

  async exec(quit: () => boolean) {
    while (!quit()) {
      const item = await this.game.queue.pop()
      await this.game.game.mainBroadcast.slot.exec(item)
    }
  }
}

export class SlaveGame<
  InputMessage,
  OutputMessage,
  G extends Game<InputMessage, OutputMessage>
> {
  game: G
  loop: SlaveEventLoop<InputMessage, OutputMessage, G>
  queue: AsyncQueue<InputMessage>
  mainSlot: Slot<InputMessage>
  clientBroadcast: Broadcast<InputMessage>

  constructor(
    gameFactory: (sg: SlaveGame<InputMessage, OutputMessage, G>) => G
  ) {
    this.game = gameFactory(this)
    this.loop = new SlaveEventLoop(this)
    this.queue = new AsyncQueue()

    this.mainSlot = new Slot()
    this.mainSlot.bind(async item => {
      this.queue.push(item)
    })
    this.clientBroadcast = new Broadcast()
  }

  async poll(quit: () => boolean = () => false) {
    await this.loop.exec(quit)
  }

  bind(client: Client<InputMessage, OutputMessage, G>) {
    client.slave = this
    this.game.clientSignal.connect(client.clientSlot)
    client.clientSignal.connect(this.clientBroadcast.slot)
  }

  getClientConnection(): ClientConnection<InputMessage> {
    return {
      signal: this.clientBroadcast.signal,
      slot: this.mainSlot
    }
  }
}

export interface ClientConnection<InputMessage> {
  signal: Signal<InputMessage>
  slot: Slot<InputMessage>
}

export class MasterGame<InputMessage> {
  connections: ClientConnection<InputMessage>[]
  queue: AsyncQueue<InputMessage>

  constructor(conns: ClientConnection<InputMessage>[]) {
    const pushSlot = new Slot<InputMessage>()
    pushSlot.bind(async item => {
      this.queue.push(item)
    })
    this.connections = conns
    this.queue = new AsyncQueue()
    conns.forEach(c => {
      c.signal.connect(pushSlot)
    })
  }

  async poll(quit: () => boolean = () => false) {
    const signal = new Signal<InputMessage>()
    this.connections.forEach(c => {
      signal.connect(c.slot)
    })
    while (!quit()) {
      const item = await this.queue.pop()
      await signal.emit(item, true)
    }
  }
}
